<script lang="ts" setup>
import { tryOnMounted } from '@vueuse/core';
import { MotionPathHelper } from 'gsap/MotionPathHelper';
import { MotionPathPlugin } from 'gsap/MotionPathPlugin';
import InterfaceStore from 'store/interface';
import { Pane } from 'tweakpane';
import { gsap } from 'vendors/gsap';
import { computed, ref, watch } from 'vue';

gsap.registerPlugin(MotionPathPlugin);
gsap.registerPlugin(MotionPathHelper);

const emit = defineEmits(['update'])

const props = defineProps({
  ratio: Number, 
  ratioEased: Number, 
  path: String 
})


const MAX = 10000
const curves = {
  one: `M0,${MAX},C0,${MAX},${MAX},0,${MAX},0`,
  curve: 'M0,10000s0-375.03,375-375c903.84-.02,56.55-290.64,1065-1065.14,723.37-555.55,2408.29-1365.28,6347.95-2471.16C9251.93,5677.75,10000,0,10000,0',
  breakpoints: `M0,10000s16.45-375,100-375c102.75,0,845.64,.03,880,0,36.62-.03,4.95-1064.51,40-1065,35.05-.49,7556.36-.52,8115.07,0C9606.39,8195.3,9940.07,0,10000,0`,
  nawak: `M0,10000 C0,10000 0.146,9635.485 375,9625 636.253,9617.692 551.901,8526.474 608.684,8522.891 673.346,8518.81 648.777,9752.481 771.873,9748.919 812.04,9747.749 932.756,7355.452 1003.872,7355.21 1064.452,7354.996 1206.938,9732.616 1291.494,9732.838 1373.167,9733.045 1266.872,7351.318 1438.036,7346.077 1545.238,7342.773 1730.56,9741.995 1920.769,9729.387 1983.117,9725.245 2122.378,6787.798 2180.686,6788.207 2294.499,6788.998 2498.76,9309.365 2557.068,9307.702 2601.077,9306.438 3112.32,6393.797 3215.754,6394.415 3250.2595,6394.621 3489.777,8567.233 3833.993,8564.132 4866.623,8554.811 6915.28638,6333.66055 7787.95,6088.7 9251.93,5677.75 10000,0 10000,0`
}
const options = ref({
  show:false,
  curve: curves.curve,
  guides: true,
  contain: false,
  legend: false,
  controls: false,
})
const marker = ref<HTMLElement>()
const vw = computed(()=>InterfaceStore.viewport.width)

const guides = {
  x:[],
  y:[600, 1000, 1280, 1920, 2560, 3840],
  both:[375, 1440],
  legend:{
    [375]: 'Figma Mobile',
    [1440]: 'Figma Desktop', 
    [600]: 'vw mobile (600)', 
    [1000]: 'layout mobile/desktop switch (1000)', 
    [1280]: 'vw 720p (1280)', 
    [1920]: 'vw HD (1920)', 
    [2560]: 'vw WQHD (2560)',
    [3840]: 'vw 4k (3840)',
  }
}

let helper:any
function createHelper(){
  return MotionPathHelper.create("#guide", {
    draggable:false,
    path: "#curve",
    onUpdate:(path:string)=>{
      emit('update', path);
    }
  });
}
// const onInit = (()=>{
tryOnMounted(()=>{
  helper = createHelper()
  addControls()
})

function addControls(){
  const controls = new Pane({
    // @ts-expect-error
    container: document.querySelector('#controls'),
  });
  const folder = controls.addFolder({ title: 'Curve', expanded: false });
  folder.addBinding(options.value, 'show');
  folder.addBinding(options.value, 'curve', {options:curves});
  folder.addBinding(options.value, 'contain');
  folder.addBinding(options.value, 'guides');
  folder.addBinding(options.value, 'legend');
  folder.addBinding(options.value, 'controls');
}

// recreate motionHelper when curve change
watch(()=>options.value.curve, curve=>{
  emit('update', curve);

  helper?.kill()
  setTimeout(()=>{
    helper = createHelper()
  }, 500)
})

// test free / contain mode
watch(()=>options.value.contain, contain=>{
  document.documentElement.dataset.container = contain ? 'contain' : 'free'
}, {immediate:true})

watch(()=>({
  default: props.ratio,
  eased: props.ratioEased
}), (value)=>{
  setTimeout(()=>{
    gsap.set(marker.value!, {cx:`${value.default!*100}%`, cy:`${(1-value.eased!)*100}%`, transform: 'none', overwrite: true})
  }, 200)
})
</script>
    
<template>
  <div v-show="options.show"
       ref="el"
       class="demo"
  >
    <svg data-name="curves"
         xmlns="http://www.w3.org/2000/svg"
         viewBox="0 0 10000 10000"
    >
      <path id="curve"
            :d="path"
      />
      <g class="markers">
        <!-- visible - show where we are -->
        <circle id="marker"
                ref="marker"
                :cx="1440"
                :cy="10000 - 1440"
                r="50"
        />
        <!-- invisible - to use path helper -->
        <circle id="guide"
                ref="guide"
                :cx="1440"
                :cy="10000 - 1440"
                r="50"
        />
      </g>
      <g v-if="options.guides" class="guides">
        <line class="line --1-1"
              x1="0"
              :y1="10000"
              x2="10000"
              :y2="0"
        />
        <line class="line --current -x"
              :x1="vw"
              y1="0"
              :x2="vw"
              y2="10000"
        />
        <line class="line --current -y"
              x1="0"
              :y1="10000 - vw"
              x2="10000"
              :y2="10000 - vw"
        />
        <line v-for="value in guides.y"
              :key="`line-y-${value}`"
              :class="`line -y --${value}`"
              :x1="value"
              y1="0"
              :x2="value"
              y2="10000"
        />
        <line v-for="value in guides.x"
              :key="`line-x-${value}`"
              :class="`line -x --${value}`"
              :y1="10000 - value"
              x1="0"
              :y2="10000 - value"
              x2="10000"
        />

        <template v-for="value in guides.both" :key="`line-both-${value}`">
          {{ value }}
          <line :class="`line -both -x --${value}`"
                :x1="value"
                y1="0"
                :x2="value"
                y2="10000"
          />
          <line :class="`line -both -y --${value}`"
                :y1="10000 - value"
                x1="0"
                :y2="10000 - value"
                x2="10000"
          />
        </template>
      </g>
    </svg>
    
    <ul class="pannel -controls" :aria-hidden="!options.controls">
      <li><strong>Add point:</strong> ALT-Click somewhere on the path</li>
      <li><strong>Toggle smooth/corner anchor:</strong> ALT-Click the anchor</li>
      <li><strong>Get handle from corner anchor:</strong> ALT-Drag</li>
      <li><strong>Select multiple anchors:</strong> SHIFT-Click (and again to deselect)</li>
      <li><strong>Delete anchor:</strong> select it, then press DELETE key.</li>
      <li><strong>Undo:</strong> CTRL-Z</li>
      <li>Click and drag on handles to change how the path curves.</li>
    </ul>

    <ul class="pannel -actions" :aria-hidden="!options.legend">
      <li v-for="(text, w) in guides.legend"
          :key="`guide-${w}`"
          :class="`guide --${w}`"
          v-text="`------ = ${w} | ${text}`"
      />
    </ul>
  </div>
  <div id="controls" />
</template>

    
<style lang="stylus" scoped>
