  position fixed !important


[data-name="curves"]
  color deeppink
  stroke currentColor
  fill currentColor
  background-color rgba(white, 0.2)
  // stroke-width 40px
  transform scale(3)
  transform-origin bottom left
  aspect-ratio 1
  width auto
  position relative
  :deep(>*)
    pointer-events all
  +mobile()
    transform scale(2)
