  color green
  stroke-width 8px
  fill none
.line
  stroke-width 1px
  // &.--current
  // &.--1-1
  // &.--current
  //   stroke-width 2px
  &.-both
    stroke-width 4px
  &.--1000
    stroke-width 8px

#marker
#guide
  position absolute
#marker
  color deeppink
  fill rgba(deeppink, 0.4)
  stroke rgba(deeppink, 1)
  stroke-width 4
#guide
  opacity 0
  color green

.pannel
  absolute right 0
  background-color white
  padding 1em
  color black
  font-size 0.8em
  border 2px solid black
  &.-controls
    width 50%
    top 0
  &.-actions
    bottom 0

.demo :deep(strong)
  font-weight bold
:deep(.path-editor-selection)
  >.path-editor:first-child
    stroke-width 1!important

:deep(.path-editor)
  // stroke-width 5!important

.actions
  background-color white
  color black
  border 1px solid
  padding 0.2em
  absolute bottom left
  pointer-events all
  z-index 100
  >.UiButton +.UiButton
    margin-left 0.5em

[aria-hidden="true"]
  display none

.--375
  color gold
.--1440
  color chocolate
.--600
  color aqua
.--1000
  color red
.--1280
  color cornflowerblue
.--1920
